.blog-card{
  background-color: $bg-dark-color;
  color: $default-color;
  text-decoration: none;
  @include imgHover;
  
  &__description {
    h2{
      margin-bottom: 9px;
    }
  }

  &__thumbnail{
    width: 100%;
    height: 170px;
    background-color: $bg-dark-color;
    display: grid;
    place-content: center;
    overflow: hidden;
    
    img{
      display: block;
      max-width: 100%;
      max-height: inherit;
    }
  }
  
  &__content{
    background-color: $bg-dark-color;
    padding: 12px;
    border-radius: 9px;

    p{
      font-size: 19px;
    }
  }

  @media screen and (min-width: 380px){
    &__content{
      @include card;
    }
  }
}